import React from "react";
import classes from "./MainButton.module.css";

const MainButton = ({ style, text, onClick, form }) => {
	const cls = [classes.button, form === "blue" ? classes.blue : (form==='transparent') ? classes.transparent : classes.white];
	return (
		<button className={cls.join(" ")} onClick={onClick} style={{ ...style }}>
			{text}
		</button>
	);
};

export default MainButton;
