import React from 'react';
import classes from './PlayButton.module.css';

const PlayButton = ({sign}) => {
    return (
    <div>
        <button className={classes.playButton}>
		    <i className={classes.playIcon + " fa fa-play"}></i>
		</button>
		<p className={classes.playButtonSign}>{sign || "Watch now"}</p>
    </div>
    )
}

export default PlayButton;