import React from 'react';
import classes from './Switcher.module.css';
import useWindowDimensions from '../../hooks/useWindowDimensions';

const Switcher = ({number = 5, onChange, current}) => {
    if (number > 5) number = 5;
    const body = [];
    for (let i = 0; i < number; i++) {
        body.push(<div onClick={() => onChange(i)} key={i} className={classes.switcherItem} style={{backgroundColor: current === i ? "#0aaee4" : null}}></div>)
    }
    const {height} = useWindowDimensions();
    return <div className={classes.switcherWrapper} style={{paddingTop: (height-37*number)/2}}>
        {body}
    </div>
}

export default Switcher;