import React, { useState } from "react";
import classes from "./Hero.module.css";
import useWindowDimensions from "../../hooks/useWindowDimensions";
import HeroShortDescription from "../HeroShortDescription/HeroShortDescription";
import HeroWatch from "../HeroWatch/HeroWatch";
import HeroSignUp from "../HeroSignUp/HeroSignUp";
import Switcher from "../Switcher/Switcher";

const Hero = () => {
	const [current, setCurrent] = useState(0);
	const [movies] = useState([
		{
			name: "La La Land",
			genres: ["Comedy", "Drama", "Music"],
			time: 128,
			score: 8,
			background:
				"https://avatars.mds.yandex.net/get-kinopoisk-blog-post-thumb/39785/47f2de9778bc115a36d467fb3b473b2c/orig",
		},
		{
			name: "Sherlock",
			genres: ["Crime", "Drama", "Mystery"],
			time: 90,
			score: 8.9,
			background: "https://simkl.in/fanart/65/652470a02cfb2cc6_0.jpg",
		},
		{
			name: "The Gentelmen",
			genres: ["Action", "Comedy", "Crime"],
			time: 113,
			score: 8.5,
			background: "https://blog-st.ru/wp-content/uploads/2020/02/orig.jpg",
		},
		{
			name: "Afro Samurai",
			genres: ["Animation", "Action", "Adventure"],
			time: 26,
			score: 7.3,
			background:
				"https://image.winudf.com/v2/image/Y29tLkFmcm9TYW11cmFpd2FsbHBhcGVyLnBhbmRhd2FhcHBfc2NyZWVuXzFfMTUyMjgwMzc1Ml8wNjY/screen-1.jpg?fakeurl=1&type=.jpg",
		},
		{
			name: "Fight Club",
			genres: ["Drama", "Crime", "Thriller"],
			time: 149,
			score: 8.8,
			background:
				"https://ilovehdwallpapers.com/wallpapers/fight-club-1920x1080.jpg",
		},
	]);

	const { height, width } = useWindowDimensions();

	return (
		<div
			className={classes.hero}
			style={{
				height: height,
				background: `url('${movies[current].background}')`,
			}}
		>
			<div
				className={classes.shadowTop}
				style={{ width: width - 100, height: height * 0.3 }}
			></div>
			<div
				className={classes.shadowBottom}
				style={{ width: width - 100, height: height * 0.3 }}
			></div>

			<Switcher
				onChange={(i) => setCurrent(i)}
				number={movies.length}
				current={current}
			/>
			<HeroShortDescription
				movieName={movies[current].name}
				movieGenres={movies[current].genres}
				movieTime={movies[current].time}
				movieScore={movies[current].score}
			/>
			<HeroWatch />
			<HeroSignUp />
		</div>
	);
};

export default Hero;
