import React from 'react';
import classes from './HeroWatch.module.css';
import MainButton from '../MainButton/MainButton';
import FavoritesButton from '../FavoritesButton/FavoritesButton';
import Menu from '../Menu/Menu';

const HeroWatch = () => {
    return <div className={classes.wrapper}>
        <MainButton form="blue" text="Watch now"  style={{marginRight: 30}}/>
        <MainButton text="View info" style={{marginRight: 30}}/>
        <FavoritesButton form="white" style={{marginRight: 15}}/>
        <Menu onClick={() => alert('Finish this section before leaving!')}/>
    </div>
}

export default HeroWatch;