import React from 'react';
import classes from './HeroSignUp.module.css';
import MainButton from '../MainButton/MainButton';
import Search from '../Search/Search';

const HeroSignUp = () => {
    return <div className={classes.wrapper}>
        <Search style={{marginRight: 20}} placeholder='Search movies'/>
        <MainButton text="Sign In" style={{marginRight: 20}}/>
        <MainButton form="blue" text="Sign Up"/>
    </div>

    
}

export default HeroSignUp;