import React from "react";
import classes from "./Menu.module.css";

const Menu = ({ style, size, number = 3, onClick }) => {
	let body = [];
	for (let i = 0; i < number; i++) {
		body.push(
			<span
				key={i}
				className={classes.dot}
				style={{
					width: size,
					height: size,
				}}
			></span>,
		);
	}
	return (
		<span className={classes.wrapper} style={{ ...style }} onClick={onClick}>
			{body}
		</span>
	);
};

export default Menu;
